.follow {
  padding-top: 100px;
  position: relative;
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  .videoCardMask {
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0) 80%, rgba(0, 0, 0, 0));
    inset: 0;
    position: absolute;
  }
  .followItem {
    max-width: calc(100% / 3);
    flex-basis: calc(100% / 3);
    padding: 6px;
    cursor: pointer;
  }
  .videoContainer {
    position: relative;
  }
  .video {
    width: 100%;
    border-radius: 8px;
    height: 303px;
    object-fit: cover;
  }
  .videoContent {
    position: absolute;
    bottom: 30px;
    width: 100%;
    text-align: center;
    color: #fff;
    .fullname {
      font-size: 1.8rem;
    }
    .nickname {
      font-size: 1.4rem;
      line-height: 2.5;
      .icon {
        color: rgb(32, 213, 236);
      }
    }
  }
}

@media screen and (max-width: 500px) {
  .follow .followItem {
    max-width: 50%;
    flex-basis: 50%;
  }
}
